const ajax =
  new XMLHttpRequest(); /* Î∏åÎùºÏö∞Ï†ÄÍ∞Ä ÎπÑÎèôÍ∏∞ Ï≤òÎ¶¨Î•º ÏúÑÌï¥ Ï†úÍ≥µÌïòÎäî ÎÇ¥Ïû•Í∞ùÏ≤¥ */
const TITLE_URL = "https://api.hnpwa.com/v0/news/1.json";
const container = document.getElementById("root");
const content = document.createElement("div");
const CONTENT_URL = "https://api.hnpwa.com/v0/item/@id.json";

//ÏóêÎ°úÏö∞ function Î∞©Ïãù
getData = (url) => {
  //arrow function ÏúºÎ°ú Ï≤òÎ¶¨ - ES6Ï∂îÍ∞ÄÎêú Ïª®Î≤§ÏÖò
  ajax.open("GET", url, false);
  ajax.send();
  return JSON.parse(ajax.response);
};

getNewsData = () => {
  const newsData = getData(TITLE_URL);
  const newsList = [];
  newsList.push("<ul>");
  for (let i = 0; i < 10; i++) {
    newsList.push(`
      <li>
        <a href = "#${newsData[i].id}">
          ${newsData[i].title}(${newsData[i].comments_count})
        </a>
      </li>
    `);
  }
  newsList.push("</ul>");
  container.innerHTML = newsList.join("");
}; //end of getNewsData

newsDetail = () => {
  const id = location.hash.substring(1);
  const ncontent = getData(CONTENT_URL.replace("@id", id));
  container.innerHTML = `
  <h1>${ncontent.title}</h1>
  <div><a href="#">Î™©Î°ùÏúºÎ°ú</a></div>
  `;
}; //end of newsDetail

router = () => {
  const routerPath = location.hash;
  console.log(routerPath);
  if (routerPath === "") {
    // === : ÌÉÄÏûÖÍπåÏßÄ ÎπÑÍµê
    getNewsData(); //Í∏ÄÎ™©Î°ù Î≥¥Í∏∞
  } else {
    newsDetail(); //Í∏Ä ÎÇ¥Ïö© Î≥¥Í∏∞
  }
};

addEventListener("hashchange", router);
router();
